import * as Yup from "yup";
import axios from "../../axios";
import { DropzoneArea } from "material-ui-dropzone";
import Alert from "@material-ui/lab/Alert";
import React, { Component } from "react";
import { Formik, Form, Field } from "formik";
import { Button, LinearProgress } from "@material-ui/core";
import { TextField } from "formik-material-ui";
import { connect } from "react-redux";
const mapStateToProps = (state) => ({
  Dept_ID: state.Dept_ID,
  collegeID: state.collegeID,
});

class addDeptInfo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      deptInfo: "",
      HOD_msg: "",
      Dept_HOD: "",
      file: "",
      base64: "",
      fileerror: false,
    };
  }
  handleFileChange = (files) => {
    if (files[0] !== undefined) {
      let reader = new FileReader();
      reader.readAsDataURL(files[0]);
      reader.onloadend = () => {
        this.setState({
          file: files[0],
          base64: reader.result,
          fileerror: false,
        });
      };
      alert(reader.result);
    }
  };
  componentDidMount() {
    axios
      .get(
        `api/departmentsinfo/` +
          this.props.Dept_ID +
          "/" +
          this.props.collegeID,
        {}
      )
      .then((res) => {
        let array = res.data;
        for (let i in array) {
          this.setState({
            deptInfo: array[i].Dept_intro,
            HOD_msg: array[i].HOD_msg,
            Dept_HOD: array[i].Dept_HOD,
          });
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }
  render() {
    return (
      <div class="container">
        <div class="row">
          <div class="col-sm">
            <div class="card">
              <div class="card-body">
                <h5 class="card-title">Add About Department</h5>
                <Formik
                  enableReinitialize={true}
                  initialValues={{
                    Dept_intro: this.state.deptInfo,
                    HOD_msg: this.state.HOD_msg,
                    Dept_HOD: this.state.Dept_HOD,
                  }}
                  validationSchema={Yup.object({
                    Dept_intro: Yup.string().required("Required"),
                    HOD_msg: Yup.string().required("Required"),
                    Dept_HOD: Yup.string().required("Required"),
                  })}
                  onSubmit={(values, { setSubmitting, resetForm }) => {
                    setTimeout(() => {
                      values.HOD_photo = this.state.base64;
                      setSubmitting(false);
                      axios
                        .post(
                          "api/departmentsinfo/" +
                            this.props.Dept_ID +
                            "/" +
                            this.props.collegeID,
                          JSON.stringify(values),
                          {
                            headers: {
                              // Overwrite Axios's automatically set Content-Type
                              "Content-Type": "application/json",
                              Accept: "application/json",
                            },
                          }
                        )
                        .then((res) => {
                          if (res.status === 200) {
                            alert("Data Saved in Database :", res.data.flag);
                          }
                        })
                        .catch(function (error) {
                          alert("Error occured:" + error);
                        });
                      resetForm();
                    }, 500);
                  }}
                >
                  {({ submitForm, isSubmitting, values }) => (
                    <Form>
                      <div class="formspacer " />
                      <div class="row justify-content-start">
                        <div class="col">
                          <Field
                            component={TextField}
                            name="Dept_intro"
                            type="text"
                            label="About Department "
                            variant="outlined"
                            fullWidth
                            multiline
                            rows={10}
                          />
                          <br />
                          <br />
                          <Field
                            component={TextField}
                            name="HOD_msg"
                            type="text"
                            label="HOD Massege "
                            variant="outlined"
                            fullWidth
                            multiline
                            rows={5}
                          />
                          <br />
                          <br />
                          {this.state.fileerror ? (
                            <Alert severity="error">
                              {" "}
                              Photograph of the HOD (max 50Kbs) in JPEG Format
                              only{" "}
                            </Alert>
                          ) : (
                            <Alert severity="info">
                              Select PP Size Photograph of the HOD (max 50Kbs)
                              in JPEG{" "}
                            </Alert>
                          )}
                          <DropzoneArea
                            acceptedFiles={["image/jpeg"]}
                            maxFileSize={50000}
                            filesLimit={1}
                            dropzoneText="HOD Photograph"
                            showPreviewsInDropzone={true}
                            onChange={this.handleFileChange}
                          />
                        </div>
                      </div>
                      <div class="formspacer " />

                      <Field
                        component={TextField}
                        name="Dept_HOD"
                        type="text"
                        label="HOD Name "
                        variant="outlined"
                        fullWidth
                      />

                      <div class="row justify-content-start"></div>
                      <div class="formspacer " />
                      <div class="row justify-content-start">
                        <div class="col"></div>
                      </div>
                      <div class="formspacer " />
                      <div class="formspacer " />
                      <div class="row justify-content-start">
                        <div class="col">
                          {isSubmitting && <LinearProgress />}
                        </div>
                      </div>

                      <div class="row justify-content-end">
                        <div class="col">
                          <Button
                            variant="contained"
                            color="primary"
                            disabled={isSubmitting}
                            onClick={submitForm}
                          >
                            Submit
                          </Button>
                        </div>
                      </div>
                    </Form>
                  )}
                </Formik>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default connect(mapStateToProps)(addDeptInfo);
